details {
    background-color: var(--bg-1);
    border-radius: var(--radius-1);
    transition: .2s ease;
}

details:not([open]):hover {
    background-color: #e5e5e5;
}

details > * {
    margin: 0;
    padding: 0 2.6em 1em;
}

details blockquote {
    padding: 0 5em 2em;
}

details code {
    background-color: #e4e4e4;
    padding: .2em .5em;
}

summary {
    font-weight: bold;
    padding: 1.25em 1.5em;
}

details[open] summary {
    padding-bottom: 1em;
}