:root {
    --bg-0: #fff;
    --bg-1: #f2f2f2;
    --bg-2: var(--bg-0);
    --radius-0: .75em;
    --radius-1: .5em;
    --gap: 4em;
}

body {
    color: #333;
    background-color: var(--bg-1);
    font-size: 17px;
    line-height: 1.4;
}

#root {
    background-color: var(--bg-0);
    max-width: 70ch;
    margin: 0 auto;
    padding: 0 4em;
}

h1 {
    font-size: 2.5em;
    margin: 0 0 .25em;
    line-height: 1.1;
}

h2 {
    font-size: 1.75em;
    border-bottom: 1px solid #ccc;
    padding-bottom: .2em;
    margin: 0 0 .8em;
}

header {
    padding: var(--gap) 0;
}

header p {
    font-size: 1.25em;
    margin: 0 0 1.5em;
}

section {
    padding: 0 0 4em;
}

header > *:last-child,
section > *:last-child {
    margin-bottom: 0;
}

a {
    color: #333;
    text-decoration-thickness: 1px;
}

code {
    font-size: .9em;
    background-color: var(--bg-1);
    padding: .2em .5em;
    border-radius: .3em;
}

footer {
    color: #777;
    font-size: .85em;
    text-align: center;
    padding-bottom: 4em;
}

video {
    width: 100%;
    border-radius: var(--radius-0);
    box-shadow: 0 3px 24px -6px rgba(0, 0, 0, 0.25);
}