.tile-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5em;
    margin: 2em 0;
}

.tile {
    font-size: 1em;
    font-weight: bold;
    background-color: var(--bg-1);
    padding: 1.5em 1em;
    border: 2px solid var(--bg-1);
    border: none;
    border-radius: var(--radius-1);
    transition: .2s ease;
}

.tile:hover {
    background-color: #e5e5e5;
    border-color: #e5e5e5;
}

.tile.detected {
    border-color: green;
}

.tile.detected:not(.selected) div {
    color: green;
}

.tile.selected {
    color: white;
    background-color: green;
    border-color: green;
}