ol {
    counter-reset: section;
    padding: 0;
}

ol > li {
    margin-top: 1.5em;
    list-style: none;
}

ol > li::before {
    counter-increment: section;
    content: "Step " counter(section) ".";
    display: block;
    font-weight: bold;
}

ul {
    margin-top: 1em;
    padding: 0 0 0 2em;
}

ul > li {
    list-style: none;
    margin-top: 1em;
}